html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

.App-logo {
  float: left;
  margin-top: 15px;
  margin-left: 28px;
  font-size: 1.5em;
  height: 30px;
  letter-spacing: 1px;
  text-transform: uppercase;
} @media(max-width: 768px) {
  .App-logo {
    /*       margin-left: 5px; */
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-navbar {
  background: white;
  color: rgb(13, 26, 38);
  position: fixed;
  top: 0;
  height: 60px;
  line-height: 60px;
  width: 100vw;
  z-index: 10;
}

.App-nav-wrapper {
  margin: auto;
  text-align: center;
  width: 90%;
} @media(max-width: 768px) {
  .App-nav-wrapper {
    width: 90%;
  }
} @media(max-width: 638px) {
  .App-nav-wrapper {
    width: 100%;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-banner {
  background-color: dimgray;
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-with-us {
  background-color: gray;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-middle-info {
  background-color: darkgray;
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-contact-us {
  background-color: lightgray;
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-footer {
flex-shrink: 0;
background-color: #f8f9fa;
color: black;
text-align: center;
padding: 10px;
}

/**
navigation area
 */

.App-navbar-ul {
  display: inline-block;
  float: right;
  list-style: none;
  /* margin-right: 14px; */
  margin-top: -2px;
  text-align: right;
  transition: transform 0.5s ease-out;
  -webkit-transition: transform 0.5s ease-out;
} @media(max-width: 640px) {
  .App-navbar-ul {
    display: none;
  }
} @media(orientation: landscape) {
  .App-navbar-ul {
    display: inline-block;
  }
}


.App-navbar-li {
  display: inline-block;
}

.App-navbar-li-a {
  color: rgb(13, 26, 38);
  display: block;
  font-size: 0.7em;
  height: 50px;
  letter-spacing: 1px;
  margin: 0 20px;
  padding: 0 4px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
}

/*Navigation hover*/
.App-navbar-li-a:hover {
  /* border-bottom: 1px solid rgb(28, 121, 184); */
  color: rgb(28, 121, 184);
  transition: all 1s ease;
  -webkit-transition: all 1s ease;
}

/*Navigation Bottom Line Animation*/
.App-navbar-li-a:before, .App-navbar-li-a:after {
  content: '';
  position: absolute;
  width: 0%;
  height: 1px;
  bottom: -1px;
  background: rgb(13, 26, 38);
}

.App-navbar-li-a:before {
  left: 0;
  transition: 0.5s;
}

.App-navbar-li-a:after {
  background: rgb(13, 26, 38);
  right: 0;
  /* transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1); */
}

.App-navbar-li-a:hover:before {
  background: rgb(13, 26, 38);
  width: 100%;
  transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.App-navbar-li-a:hover:after {
  background: transparent;
  width: 100%;
  /* transition: 0s; */
}

.App-navbar-button {
  float: right;
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-flex;
  margin-top: 7px;
  margin-left: 50px;
  font-size: 16px;
  cursor: pointer;
}